import Head from 'next/head';
import React, { Component, Fragment } from "react";
import PropTypes from 'prop-types';
import Menu from '../components/Menu/Menu';
import Footer from "../components/Footer/Footer";
import Ribbon from "../components/Ribbon/Ribbon";
import Stats from '../util/stats';
import detect from '../util/detect/index.js';
import ReactGA from 'react-ga';
import settings from '../util/settings';

export default class Page extends Component {

  componentDidMount() {
    if (settings.isDev) Stats();
    detect.init();
    document.body.className = [...new Set([...document.body.className.split(' '), ...detect.classes()])].join(' ');
    ReactGA.initialize(settings.GACcode)
    ReactGA.pageview(window.location.pathname)

    if (!settings.isServer()) {
      window.addEventListener('resize', this.handleResize);
      this.handleResize();
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    if (!settings.isServer()) {
      const orientation = window.innerHeight >= window.innerWidth ? "portrait" : "landscape";
      const prevOrientation = document.body.className.indexOf('portrait') ? 'portrait' : 'landscape';
      document.body.className = document.body.className.replace(prevOrientation, orientation);
    }
  }

  render() {
    const { id, children, className, menu } = this.props;
    return (
      <div id={id || ""} className={`Page${className ? ` ${className}` : ''}`}>
        { (!menu || menu.show !== false) &&
          <Fragment>
            <Menu location={settings.isServer() ? {} : window.location} solidStyle={this.props.menu.solidStyle} />
            <Ribbon />
          </Fragment>
        }
        <Fragment>
          { children }
        </Fragment>
        <Footer />
      </div>
    )
  }
}

Page.propTypes = {
  id: PropTypes.string,
  children: PropTypes.array,
  className: PropTypes.string,
  menu: PropTypes.object
}

Page.defaultProps = {
  id: '',
  children: [],
  className: '',
  menu: {}
}