import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Link from 'next/link';
import Social from '../Social/Social';
import settings from '../../util/settings';

export default class Footer extends Component {
  constructor(props) {
    super(props);
    this.ASSET_PATH = settings.ASSET_PATH + "images/";
    this.resources = [
      { title: 'Quick Links',
        links: [
          { title: 'Our Services',
            url: '/services'
          },
          { title: 'About Us',
            url: '/about'
          },
          { title: 'Testimonials',
            url: '/testimonials'
          },
          { title: 'Contact Us',
            url: '/contact'
          }
        ]
      },
      { title: 'Resources',
        links: [
          { title: 'FAQ',
            url: '/faq'
          },
          { title: 'Careers',
            url: '/careers'
          }
        ]
      },
      { title: 'Legal',
        links: [
          { title: 'Privacy Policy',
            url: '/privacy'
          }
        ]
      }
    ]
  }

  render() {
    return (
      <div className="Footer" ref={el => this.Footer = el}>
        <div className="row container">
          <div className="row col-md-8 resource-groups-container">
            { this.resources.map( (resource, index) => {
                return (
                  <div className="col-6 col-md-3 resource-group" key={ index }>
                    <p className="resource-group-title mini">{ resource.title }</p>
                    { resource.links.map( (link, idx) => {
                        return link.title &&
                          <Link href={link.url} key={ idx } prefetch>
                            <a className="resource-link mini">{ link.title }</a>
                          </Link>
                      })
                    }
                  </div>
                )
              })
            }
          </div>
          <div className="col-md-4 footer-logo-container">
            <img src={this.ASSET_PATH + "brand-landing.png"} alt="Hospall HomeCare" className="footer-logo"/>
            <div className="d-none d-md-block">
              <p className="dark mini">Hospall Private Homecare provides peace of mind to families concerned with the health and care needs of their loved ones.</p>
              <Social />
            </div>
          </div>
        </div>
        <div className="row container">
          <div className="d-block d-md-none" style={{ marginTop: 20, marginBottom: 30 }}>
            <Social align={!settings.isServer() && window.innerWidth <= 767 ? 'center' : 'left'} />
          </div>
          <p className="mini copyright">&copy; 2019 HOSPALL PRIVATE HOME CARE INC. ALL RIGHTS RESERVED.</p>
        </div>
      </div>
    );
  }
}

Footer.propTypes = {
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string,
  image: PropTypes.string,
  className: PropTypes.string
};

Footer.defaultProps = {
  title: "Title"
};
